@import 'smart-grid.less';
@import 'storage.less';
@import 'colors.less';

.reset();

body{
	background: url(../img/bg.png) repeat;
}

a{
	cursor: pointer;
}

.wrapper{
	.wrapper();
}

header{
	background: url(../img/header_bg.png) no-repeat;
	background-size: cover;
	font-family: 'Museo', sans-serif;

	.container{
		position: fixed;
		width: 100%;
		z-index: 100;
		transition: 0.2s all ease;

		.menu{
			padding: 0;
			transition: 0.2s all ease;
		}

		.menu{
			.row-flex();
			margin: 0;
			padding-top: 10px;
			
			.logo{
				.size(2);
				.size-450(2.5);
				.offset-left(0.5);
				z-index: 20;

				.logo_2{
					display: none;
				}
			}
		}

		&.fixed{
			background: rgba(0,0,0,0.3);
			z-index: 100;
			padding: 10px 0;
			transition: 0.2s all ease;

			.xs(padding, 15px 0 0 0);

			.menu{
				padding: 0;
				transition: 0.2s all ease;
			}

			.phone{
				display: none;
			}
		}

		nav{
			.row-flex();
			.offset-right-padding(0.3);
			margin: 20px 0 0 auto;


			.sm-block({
				margin: 0 0 0 auto;
				padding: 0 2%;

				flex-basis: 100%;
				margin-top: -40px;
			});
			.450(margin-top, -35px);
			.350(margin-top, -25px);

			.list{
				display: flex;

				.sm-block({
					width: 100%;
					background: #fff;
					padding: 20px 0px 20px 30px;
					margin-bottom: 0;
					border-radius: 3px;
					display: none;
				});


				li{
					margin: 0 5px;

					.sm-block({
						margin: 5px 15px;
						padding-left: 20px; 
					});
				
					a{
						text-decoration: none;
						font-size: 16px;
						font-weight: bold;
						color: #fff;
						transition: .2s all ease; 
						.sm(color, #000);
						
						&:hover{
							color: #817e7d;
						}

					}
				}
				&.active{
					.row-flex();
					margin: 0;
					display: flex;

					.sm-block({
						display: block;
						order: 1;						
					});
				}
			}
			.burger-icon{
				position: relative;
				height: 0px;
				padding: 13px 15px;
				margin: 0 0 0 auto;
				margin-bottom: 10px;
				cursor: pointer;
				display: none;

				.sm(display, block);

				span{
					position: absolute;
					display: block;
					width: 20px;
					height: 2px;
					background: @color_green;
					left: 0;
					right: 0;
					margin: 0 auto;
					transition: .4s all ease;

					.450(width, 15px);

					&:before{
						position: absolute;
						content: '';
						display: block;
						width: 20px;
						height: 2px;
						background: @color_green;
						top: -5px;
						left: 0;
						right: 0;
						margin: 0 auto;
						transition: .4s all ease;
						
						.450(width, 15px);
					}
				}

				&_active span{
					transform: rotate(-45deg);
					top: 39%;

					&:before{
						transform: rotate(90deg);
						top: 0;

					}
				}
			}
		}
		.phone{
			flex-basis: 80%;
			color: @color_green;
			text-align: right;
			margin: -40px 0 0 auto;

			.md(margin-top, -25px);
			.850(margin-top, -20px);
			.800(margin-top, -15px);


			a{
				color: @color_green;
				text-decoration: none;
			}

			.offset-right-padding(0.3);

			.sm-block({
				margin-top: -15px;
				padding-right: 3%;
			});

			.xs(padding-right, 3.5%);
			.450(padding-right, 3.9%);
			.350(padding-right, 4.4%);



			.icon{
				display: none;
				color: @color_green;
				font-size: 20px;

				.sm(display, block);
				.450(font-size, 16px);
			}

			.number, .schedule{
				.sm(display, none);
			}
		}

		.margin{
			.sm(margin-top, -30px);
		}
	}


	.wrapper{
		max-width: 1250px;
		.slider{
			.slide{
				position: relative;
				padding-bottom: 40px;

				.shadow{
					position: absolute;
					width: 94%;
					height: 93%;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					margin: 0 auto;
					z-index: -10;
					box-shadow: 0px 10px 25px 5px rgba(0,0,0,0.75);
					
					.lg(height, 92%);
					.md(height, 91%);
					.800(height, 90%);
					.sm-block({
						width: 91%;
						height: 88%;
					});
					.xs(height, 85%);
					.450-block({
						width: 86%;
						height: 82%;
					});
					.400-block({
						width: 84%;
						height: 79%;
					});
					.350-block({
						width: 83%;
						height: 78%;
					});

				}

				.slide_img{
					position: relative;
					width: 96%;
					margin: 0 auto;

					img{
						width: 100%;
					}

				}

				.slide_info{
					position: absolute;
					top: 40%;
					right: 0;
					left: 0;
					.size(3.5);
					.size-850(4.5);
					.size-xs(7.5);
					.size-350(8);
					margin: auto;
					text-align: center;
					color: @color_green;

					.md(top, 35%);
					.800(top, 25%);
					.400(top, 20%);
					.350(top, 15%);

					.text{
						font-size: 25px;

						.800(font-size, 23px);
						.450(font-size, 18px);
						.400(font-size, 16px);
					}

					.price{
						font-size: 32px;
						margin: 15px 3% 15px 0;

						.800(font-size, 30px);
						.sm(font-size, 28px);
						.xs(font-size, 24px);
						.450(font-size, 20px);
						.400-block({
							font-size: 18px;
							margin: 5px 3% 5px 0;
						});

						span{
							position: relative;
							font-size: 24px;
							color: @color_red;
							&:after{
								position: absolute;
								content: '';
								left: -5px;
								bottom: 13px;
								width: 94%;
								transform: rotate(-25deg);
								border-bottom: 3px solid @color_green;
								
								.400-block({
									bottom: 10px;
									border-bottom: 2px solid @color_green;
								});

							}

							.800(font-size, 22px);
							.sm(font-size, 20px);
							.450(font-size, 16px);

						}
					}

					.buy{
						a{
							text-decoration: none;
							text-transform: uppercase;
							display: inline-block;
							font-size: 19px;
							padding: 1px 3px;
							border: 1px solid @color_green;

							.xs(font-size, 16px);
							.450(font-size, 14px);
						}
					}
				}
			}

			.slide_2{
				.slide2_info{
					position: absolute;
					top: 25%;
					left: 0;
					.size(6);
					.size-800(8);
					.size-xs(9);
					.size-400(12);
					.offset-left(0.7);
					.offset-left-xs(1);
					.offset-left-400(0.2);
					
					.md(top, 17%);

					h2{
						color: @color_green;
						font-size: 30px;

						.sm(font-size, 28px);
						.xs(font-size, 26px);
						.450(font-size, 20px);
						.400(font-size, 16px);
						.350(font-size, 14px);
					}

					.h2sub{
						font-size: 20px;
						color: #e1e1e1;

						.sm(font-size, 18px);
						.xs(font-size, 16px);
						.450(font-size, 14px);
						.400(font-size, 12px);
					}

					.text{
						position: relative;
						font-size: 15px;
						color: #79ecc2;
						margin: 2% 0;
						
						.xs(display, none);

					}

					.buy{
						.450(margin-top, 10px);
						.350(margin-top, 0);
						a{
							text-decoration: none;
							text-transform: uppercase;
							display: inline-block;
							color: @color_green;
							font-size: 19px;
							padding: 1px 3px;
							border: 1px solid @color_green;

							.xs(font-size, 16px);
							.450(font-size, 14px);
							.350(font-size, 12px);
						}
					}

				}
			}

			.slide_3{
				.slide3_info{
					position: absolute;
					top: 18%;
					left: 5%;
					.size(6);
					.size-md(7);
					.size-sm(8);
					.size-xs(10);
					.size-450(10);
					.size-400(12);
					
					.offset-left-xs(0.3);
					.offset-left-400(0.2);
					.xs(left, 0);

					h2{
						font-size: 30px;
						color: @color_green;

						.sm(font-size, 26px);
						.xs(font-size, 24px);
						.450(font-size, 20px);
						.400(font-size, 16px);
						.350(font-size, 14px);
					}

					.h2sub{
						font-size: 26px;
						color: @color_green;

						.md(font-size, 24px);
						.sm(font-size, 22px);
						.xs(font-size, 20px);
						.450(font-size, 16px);
						.400(font-size, 14px);
						.350(font-size, 12px);
					}
					
					.text{
						text-align: right;
						margin-top: 20%;

						.md(margin-top, 10%);
						.md(margin, 0);
						.sm(margin-right, 25%);
						.450(text-align, center);
						.400(text-align, left);

						.price{
							display: inline-block;
							font-size: 32px;
							text-align: center;
							margin: 15px 0;
							color: @color_green;
							
							.800(font-size, 30px);
							.sm-block({
								font-size: 28px;
								margin: 0;
							});
							.xs(font-size, 22px);
							.450-block({
								font-size: 20px;
								margin: 10px 12% 10px 0;
							});
							.400-block({
								font-size: 18px;
								margin: 0px 0 10px 5%;
							});
							.350(font-size, 16px);

							span{
								position: relative;
								font-size: 24px;
								color: @color_red;
								&:after{
									position: absolute;
									content: '';
									left: -5px;
									bottom: 13px;
									width: 94%;
									transform: rotate(-25deg);
									border-bottom: 3px solid @color_green;
								
									.400-block({
										bottom: 10px;
										border-bottom: 2px solid @color_green;
									});

								}

								.800(font-size, 22px);
								.sm(font-size, 20px);
								.450(font-size, 16px);
								.350(font-size, 14px);

							}

							.buy{
								a{
									text-decoration: none;
									display: inline-block;
									font-size: 19px;
									padding: 1px 3px;
									border: 1px solid @color_green;

									.xs(font-size, 16px);
									.450(font-size, 14px);
									.350(font-size, 12px);
								}
							}
						}
					}
					
				}
			}

			.slide_4{
				.shadow{
					position: absolute;
					width: 85%;
					height: 92%;
					top: 0;
					right: 0;
					bottom: 0;
					left: 1.8%;
					margin: 0 auto;
					z-index: -10;
					box-shadow: 0px 10px 25px 5px rgba(0,0,0,0.75);
					
					.800(height, 89%);
					.sm(height, 88%);
					.xs-block({
						width: 84%;
						height: 85%;
					});
					.450(height, 82%);

				}

				.slide4_info{
					position: absolute;
					top: 18%;
					right: 14%;
					.size(9);
					.size-sm(12);

					.sm(right, 6%);
					.400(right, 5%);

					h2{
						font-size: 30px;
						color: @color_green;
						text-align: right;

						.md(font-size, 28px);
						.800(font-size, 26px);
						.xs(font-size, 20px);
						.450(font-size, 16px);
						.400(font-size, 14px);
						.350(font-size, 12px);
					}

					.h2sub{
						font-size: 24px;
						color: @color_green;
						text-align: right;

						.800(font-size, 22px);
						.xs(font-size, 18px);
						.450(font-size, 14px);
						.400(font-size, 13px);
					}
					
					.text{
						text-align: center;
						margin-top: 8%;

						.sm(margin-top, 0);
						.350(text-align, left);

						.price{
							display: inline-block;
							font-size: 32px;
							text-align: center;
							margin: 15px 0 15px 13%;
							color: @color_green;

							.800(font-size, 30px);
							.sm-block({
								font-size: 28px;
								margin: 5px 0 5px 10%;
							});
							.xs(font-size, 24px);
							.450-block({
								font-size: 20px;
								margin: 10px 0 15px -20%;
							});
							.400-block({
								font-size: 18px;
								margin: 0 0 15px -20%;
							});
							.350-block({
								font-size: 16px;
								margin: 0 0 15px 8%;
							});

							span{
								position: relative;
								font-size: 24px;
								color: @color_red;
								&:after{
									position: absolute;
									content: '';
									left: -5px;
									bottom: 13px;
									width: 94%;
									transform: rotate(-25deg);
									border-bottom: 3px solid @color_green;
									
									.400-block({
										bottom: 10px;
										border-bottom: 2px solid @color_green;
									});

								}

								.800(font-size, 22px);
								.sm(font-size, 20px);
								.400(font-size, 16px);
								.350(font-size, 14px);

							}

							.buy{
								a{
									text-decoration: none;
									display: inline-block;
									font-size: 19px;
									padding: 1px 3px;
									border: 1px solid @color_green;

									.xs(font-size, 16px);
									.450(font-size, 14px);
									.350(font-size, 12px);
								}
							}
						}
					}
					
				}
			}

			.owl-dots{
				position: absolute;
				margin-top: 0 !important;
				bottom: 18%;
				right: 15%;

				.owl-dot{
					span{
						background: #fff;
						width: 10px;
						height: 10px;
						.xs-block({
							width: 8px;
							height: 8px;
						});
						.350-block({
							width: 5px;
							height: 5px;
						});
					}
				}				

				.active{
					span{
						width: 12px;
						height: 12px;
						margin: 4px;
						background: @color_green;
						.xs-block({
							width: 10px;
							height: 10px;
						});
						.350-block({
							width: 8px;
							height: 8px;
						});
					}
				}
			}
		}
	}
}

.up{
	font-family: 'Museo';
	position: fixed;
	display: flex;
	right: 2%;
	bottom: 10%;
	z-index: 99;
	transform: rotate(90deg);
	font-size: 20px;
	color: #303030;
	cursor: pointer;
	display: none;

	i{
		font-size: 12px;
		margin: auto 5px;
		color: @color_green;
	}
}

.advantages{
	font-family: 'Lucida', sans-serif;
	.wrapper{
		.heading{
			margin-top: 105px;
			text-align: center;

			.800(margin-top, 50px);
			.xs(margin-top, 0);

			h2{
				position: relative;
				display: inline-block;
				font-size: 48px;
				color: #000;

				.800(font-size, 46px);
				.xs(font-size, 42px);
				.450(font-size, 36px);

				&:after{
					position: absolute;
					content: '';
					bottom: 0;
					left: 1%;
					width: 98%;
					border-bottom: 2px solid #df0024;
				}
			}
		}

		.items{
			.row-flex();
			justify-content: center;
			margin: 0;

			.item{
				.col();
				display: flex;
				.size(4);
				.size-800(6);
				.size-450(10);
				.offset-450(1);
				margin-top: 80px;

				.sm(margin-top, 50px);
				.xs(margin-top, 25px);

				.450-block({
					flex-wrap: wrap;
					text-align: center;
					justify-content: center;
				});

				.img{
					.size(2.5);
					.size-850(3);
				}

				.text{
					.size(12);
					font-size: 16px;
					margin-left: 5px;
					color: #2a322e;
					line-height: 1.2;

					h3{
						font-size: 17px;
						color: #f00;
					}
				}
			}
		}

		.warning{
			h2{
				font-size: 30px;
				font-weight: bold;
				text-align: center;
				color: #f20b0b;
				margin-top: 85px;

				.800-block({
					font-size: 28px;
					margin-top: 60px;
				});

				.xs(font-size, 26px);
				.450(font-size, 20px);
			}
		}
	}
}

.cases{
	font-family: 'Lucida', sans-serif;
	padding: 60px;
	position: relative;
	background: url(../img/cases_bg.png) no-repeat;
	background-size: cover;
	background-position: center;
	.850(padding, 60px 0 50px 0);
	.sm(padding, 50px 0 40px 0);
	.xs(padding, 20px 0 10px 0);

	.wrapper{
		.heading{
			h2{
				font-size: 48px;
				font-weight: bold;
				text-align: center;
				line-height: 1.2;

				.800(font-size, 42px);
				.xs(font-size, 38px);
				.450(font-size, 32px);
				.400(font-size, 28px);
			}

			.text{
				.size(8);
				.size-md(12);
				margin: 0 auto;
				font-size: 20px;
				text-align: center;
				line-height: 1.2;
				
				.sm(font-size, 18px);
				.450(font-size, 16px);
			}
		}

		.items{
			.row-flex();
			margin: 50px 0 0 0;

			.item{
				.row-flex();
				.size(12);
				margin: 0;

				.sm(flex-basis, 100%);
				
				
				.img{
					.col();
					.size(2.5);
					.size-md(3);
					.size-sm(8);
					.size-xs(12);
					
					display: flex;
					flex-wrap: wrap;
					flex-direction: column;
					justify-content: center;

					.sm-block({
						flex-basis: 100%;
						justify-content: center;
					});
					margin-right: 15px;
				}

				.cover{
					.size(12);
					.size-sm(12);
					.sm(flex-basis, 100%);
					
					.sm_head{
						display: none;
						font-size: 26px;
						
						.sm-block({
							display: block;
							text-align: center;
						});
						.450(font-size, 24px);
						.400(font-size, 22px);
						.350(font-size, 20px);
						}

					.info{
						width: 100%;
						line-height: 1.2;
						font-size: 16px;
						display: flex;

						.sm(flex-wrap, wrap);
						
						.wrap{
							.size(8.8);
							.size-md(10);
							.size-sm(12);
							h2{
								font-size: 30px;

								.sm-block({
									display: none;
								});

								.xs(font-size, 26px);
								.450(font-size, 24px);
								.400(font-size, 22px);
								.350(font-size, 20px);
							}

							p{
								text-indent: 4%;
								margin-top: 25px; 	

								.sm(margin-top, 0);

								.more{
									.400(display, none);
								}
							}

							.show_more{
								display: none;
								text-indent: 0;
								margin-top: 10px;
								.400(display, inline-block);

								&:hover{
									color: darken(#000, -50%); 
								}
							}

						}

						.mr{
							.offset-right(1.9);
						}

						.gift{
							display: flex;
							flex-wrap: wrap;
							align-content: center;
							
							.size(3);
							.size-xs(6);
							text-align: center;
							font-size: 16px;
							margin-left: 10px;

							.800(font-size, 14px);
							.sm-block({
								margin: 15px auto;
								justify-content: center;
							});							
						}
					}

					.composition{
						margin-top: 20px;
						ul{
							li{
								font-size: 16px;
							}
						}
					}

					
				}

				.options{
						.row-flex();
						.col();
						.size(10);
						justify-content: space-around;
						
						.800-block({
							justify-content: center;
							text-align: center;
