				}
			}

			.size{
				h2{
					font-size: 14px;
					font-weight: bold;
					margin: 10px 0;
				}

				.select-quantity{
					position: relative;
					display: inline-block;
					
					&:before {
						font-family: 'FontAwesome';
						content: "\f078";
						padding: 0 8px;
						font-size: 11px;
						position: absolute;
						color: #b6b9b9;
						right: 8px;
						top: 10px;
						z-index: 1;
						text-align: center;
						height: 100%;
						pointer-events: none;
						box-sizing: border-box;
					}

					select{
						font-size: 16px;
						height: 29px;
						width: 200px;
						padding: 0 0 0 10px;
						font-size: 12px;
						appearance: none;
						border: 1px solid #b6b9b9;
						border-radius: 3px; 
						color: #000;
					}
				}
			}


			.calc{
				.row-flex();
				margin: 0;
				justify-content: space-around;
				padding-top: 5px;

				.amount{
					.row-flex();
					margin: 0;
					align-items: center;
				
					.minus, .plus{
						position: relative;
						border: 1px solid #000;
						border-radius: 50%;
						margin: 0 5px;
						width: 14px;
						height: 14px;
						cursor: pointer;

						&:before{
							position: absolute;
							content: '';
							border-bottom: 2px solid #000;
							width: 9px;
							top: 6px;
							left: 3px;
						}

						&.plus:after{
							position: absolute;
							content: '';
							border-bottom: 2px solid #000;
							width: 9px;
							top: 6px;
							left: 3px;
							transform: rotate(90deg);
						}
					}

					.number{
						font-size: 16px;
						width: 30px;
						text-align: center;
						border: none;
					}

				}
				.price-1{
					span{
						font-size: 16px;
						font-weight: bold;
					}
				}
			}
			
			.order-form{
				h2{
					font-size: 18px;
					margin-top: 15px;
					.xs(font-size, 16px);
				}

				form{
					.row-flex();
					margin: 0;
					justify-content: center;

					input{
						.col();
						.size(7);
						.size-450(9);
						.size-400(12);

						margin: 10px 0;
						
						padding: 2px 0 2px 10px; 
						border: 1px solid #b6b9b9;
						font-size: 20px;
						text-align: center;
					}
				}

				textarea{
					.col();
					.size(7);
					.size-450(9);
					.size-400(12);

					resize: none;
					height: 37px;
					padding: 2px 0 2px 10px;
					margin-top: 15px;
					text-align: center;
					border: 1px solid #b6b9b9;
					font-size: 20px;
					transition: .2s all ease;

					&:focus{
						height: 70px;
					}
				}

				button{
					font-size: 20px;
					padding: 1px 3px;
					margin-top: 15px;
					color: #000;
					background: transparent;
					border: 1px solid #b6b9b9;
				}

				.pay{
					display: inline-block;
					a{
						display: inline-block;
						text-decoration: none;
						font-size: 18px;
						color: #000;
						padding: 1px 3px;
						line-height: 1.2;
						margin-top: 15px;
						border: 1px solid @color_red;
					}
				}

				.agreement{
					font-size: 14px;
					margin: 15px 0;
					padding-bottom: 50px;
					.400(font-size, 12px);
				}
			}
		}
	}
}

.open{
	display: block;
}